import React from "react";
import { List, Container, Header, Segment, Item } from "semantic-ui-react";
import resume from "../resume";

class Certificate extends React.Component {
  renderMetaList(meta) {
    return meta.map((listItem, itemKey) => (
      <List.Item key={itemKey}>{listItem}</List.Item>
    ));
  }

  listCertificates() {
    return resume.certificates.map((certificate, key) => {
      return (
        <Item key={key}>
        <Item.Content>
          <Segment basic clearing style={{ marginBottom: "0em" }}>
            <Header as="h1" floated="left">
              <Header.Content>
                {certificate.certificateName}
              </Header.Content>
              <Header.Subheader
                style={{ marginLeft: "1em", paddingTop: "0.5em" }}
              >
                <a href={certificate.credentialURL} target="_blank">
                  Credential
                </a>
              </Header.Subheader>
            </Header>
            <Segment
              floated="right"
              basic
              style={{ padding: "1em 0em", margin: "0em" }}
            >
              <Header as="h5" disabled style={{ padding: "0em", margin: "0em" }}>
                {certificate.date}
              </Header>
            </Segment>
          </Segment>

          <List bulleted style={{ marginTop: "0em", paddingLeft: "2em" }}>
            {this.renderMetaList(certificate.meta)}
          </List>
          </Item.Content>
        </Item>
      );
    });
  }

  render() {
    return (
      <Container text id="Certificates">
        <Segment basic clearing>
          <Item.Group divided>{this.listCertificates()}</Item.Group>
        </Segment>
      </Container>
    );
  }
}

export default Certificate;
